* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  align-self: center;
  width: 160px;
  height: 134px;
  margin: 0 0 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating-field {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__price-field {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-field-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-field-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  display: block;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.header__icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__nav .nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__nav .nav__item {
  margin-right: 20px;
}

.header__nav .nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.header__nav .nav__link:hover {
  color: #00acdc;
}

.header__nav .nav__item:last-child {
  margin-right: 0;
}

.header__nav .is-active {
  color: #00acdc;
  position: relative;
}

.header__nav .is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
